.s-solutions {
    padding: rem(150) 0 rem(155);
    background-position-x: 100%;
    background-position-y: 55%;
    background-repeat: no-repeat;
    background-size: rem(520);

    @media (max-width: 991px) {
        padding: rem(55) 0 rem(80);
        background-position: right bottom;
        background-size: 55%;
    }

    @media (max-width: 767px) {
        background-size: rem(390) rem(615);
    }

    @media (max-width: 500px) {
        padding: rem(55) 0 rem(25);
        background-size: rem(260) rem(420);
    }

    .l-default {
        max-width: rem(1520);
    }

    &__title {
        margin-bottom: rem(45);

        @media (max-width: 600px) {
            margin-bottom: rem(25);
            text-align: center;
        }
    }

    &__text {
        max-width: 80%;
        margin-bottom: rem(15);
        font-size: rem(13);
        font-weight: 400;
        line-height: 170%;
        color: $grey_light_more_2;

        @media (min-width: 768px) {
            margin-bottom: rem(15);
            font-size: rem(15);
        }

        @media (min-width: 1024px) {
            max-width: 75%;
            font-size: rem(17);
        }

        @media (min-width: 1600px) {
            font-size: rem(19);
        }

        &--last {
            max-width: 70%;
            margin-bottom: 0;
            font-size: rem(13);
            font-weight: 400;
            line-height: 170%;
            color: $grey_light_more_2;

            @media (min-width: 768px) {
                font-size: rem(15);
            }

            @media (min-width: 1024px) {
                max-width: 75%;
                font-size: rem(17);
            }

            @media (min-width: 1600px) {
                font-size: rem(19);
            }
        }

        &--short {
            margin-top: rem(96);
            margin-left: rem(100);

            @media (max-width: 1040px) {
                margin-top: rem(58);
                margin-left: rem(70);
            }

            @media (max-width: 1040px) {
                margin-left: rem(40);
            }

            .s-solutions__text {
                font-size: rem(15);
                line-height: rem(18);

                @media (min-width: 768px) {
                    font-size: rem(17);
                    line-height: rem(20);
                }

                @media (min-width: 1040px) {
                    font-size: 21px;
                    line-height: rem(25);
                }
            }
        }
    }
}
